* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

#html {
    word-break: break-all;
}

#div1 {
    position: fixed;
    top: 20px;
    left: 50%;
    transform: translateX(-50%);
}

#div1::before {
    content: '';
    display: block;
    position: absolute;
}

#div1::after {
    content: '';
    display: block;
    position: absolute;
}

@media (max-width: 500px) {
    #html {
        height: 60vh;
        overflow: hidden;
    }

    #wrapper {
        height: 40vh;
        background: #cacaca;
    }

    #div1 {
        position: relative;
        bottom: 0;
        right: 0;
        left: 50%;
        transform: translateX(-50%)
    }
}